<template>
  <div class="main">
    <div class="box">
      <div class="boxTitle">基本信息</div>
      <el-row :gutter="20">
        <el-col :span="12">
          姓名：
          <span>{{formUser.userName}}</span>
        </el-col>
        <el-col :span="12">
          身份证号：
          <span>{{formUser.userIdentity}}</span>
        </el-col>
        <el-col :span="12">
          性别：
          <span>{{formUser.userSex}}</span>
        </el-col>
        <el-col :span="12">
          毕业中学：
          <span>{{formUser.userMiddleSchool}}</span>
        </el-col>
        <el-col :span="12">
          民族：
          <span>{{formUser.userFamousRaceName}}</span>
        </el-col>
        <el-col :span="12">
          户口性质：
          <span>{{formUser.accountCharacterName}}</span>
        </el-col>
        <el-col :span="24">
          入学前户籍地址：
          <span>{{ formUser.provinceName + formUser.cityName + formUser.areaName + formUser.userPermanentAddress}}</span>
        </el-col>
      </el-row>
    </div>
    <div class="box">
      <div class="boxTitle">通讯信息</div>
      <el-row :gutter="20">
        <el-col :span="12">
          联系电话：
          <span>{{formUser.userPhone}}</span>
        </el-col>
        <el-col :span="12">
          邮政编码：
          <span>{{formUser.userPostalCode}}</span>
        </el-col>
        <el-col :span="12">
          电子邮箱：
          <span>{{formUser.userMailBox}}</span>
        </el-col>
        <el-col :span="12">
          QQ号码：
          <span>{{formUser.userQq}}</span>
        </el-col>
        <el-col :span="12">
          微信：
          <span>{{formUser.userWechat}}</span>
        </el-col>
        <el-col :span="12">
          家庭联系电话：
          <span>{{formUser.userFamilyPhone}}</span>
        </el-col>
        <el-col :span="12">
          家庭联系人：
          <span>{{formUser.userContactName}}</span>
        </el-col>
        <el-col :span="12">
          家庭联系人电话：
          <span>{{formUser.userContactPhone}}</span>
        </el-col>
        <el-col :span="24">
          通讯地址：
          <span>{{ formUser.userPostalAddress}}</span>
        </el-col>
        <!-- <el-col :span="12">
          家庭地址：
          <span>{{formUser.provinceName + formUser.cityName + formUser.areaName + formUser.userPermanentAddress}}</span>
        </el-col> -->
      </el-row>
    </div>
    <div class="box">
      <div class="boxTitle">就学信息</div>
      <el-row :gutter="20">
        <el-col :span="12">
          高校名称：
          <span>{{formUser.userUniversity}}</span>
        </el-col>
        <el-col :span="12">
          院系名称：
          <span>{{formUser.userDepartment}}</span>
        </el-col>
        <el-col :span="12">
          学历：
          <span>{{formUser.userEducation}}</span>
        </el-col>
        <el-col :span="12">
          专业名称：
          <span>{{formUser.userMajor}}</span>
        </el-col>
        <el-col :span="12">
          专业类别
          <span>{{formUser.userMajorCategory}}</span>
        </el-col>
        <el-col :span="12">
          年级：
          <span>{{formUser.userEnrollmentYear}}</span>
        </el-col>
        <el-col :span="12">
          学制：
          <span>{{ formUser.userEducationalSystem}}</span>
        </el-col>
        <el-col :span="12">
          毕业时间：
          <span>{{(formUser.userGraduationTime).substr(0,10)}}</span>
        </el-col>
        <el-col :span="12">
          学号：
          <span>{{formUser.userStudentId}}</span>
        </el-col>
      </el-row>
    </div>
    <div class="box">
      <div class="boxTitle">共同借款人信息</div>
      <el-row :gutter="20">
        <el-col :span="12">
          姓名：
          <span>{{formGuardian.guardianName}}</span>
        </el-col>
        <el-col :span="12">
          身份证号：
          <span>{{formGuardian.guardianIdentity}}</span>
        </el-col>
        <el-col :span="12">
          邮政编码：
          <span>{{formGuardian.guardianPostalCode}}</span>
        </el-col>
        <el-col :span="12">
          健康状况：
          <span>{{formGuardian.guardianHealth}}</span>
        </el-col>
        <el-col :span="24">
          手机号码：
          <span>{{formGuardian.guardianTel}}</span>
        </el-col>
        <el-col :span="24">
          户籍地址：
          <span>{{ formGuardian.provinceName + formGuardian.cityName + formGuardian.areaName + formGuardian.guardianPermanentAddress}}</span>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formUser: {},
      formGuardian:{}
    };
  },
  created() {
    this.$axios
      .post("/user/getUserDetail", {
        userLoginAccount: sessionStorage.getItem("userLoginAccount")
      })
      .then(({ status, data }) => {
        if (status == 200) {
          this.formUser = data.user;
          this.formGuardian = data.guardian;
        } else if (status == 401) {
          this.$message({
            message: "获取失败",
            type: "error"
          });
        }
      })
      .catch(error => {
        console.log(error);
      });
  }
};
</script>
<style lang="scss" scoped>
.main{
    text-align: left;
    .box{
         padding:8px 40px;
         font-size: 14px;
        .boxTitle{
            font-weight: 700;
            line-height: 20px;
            padding:8px;
            vertical-align: bottom;
    border-bottom: 3px solid #ddd;
        }
        /deep/ .el-row{
            padding:0 8px;
            .el-col{
                border-bottom: 1px solid #ddd;
                padding: 8px;
                line-height: 20px;
            }
        }
        
    }
}
</style>